import React from "react";
import cx from "classnames";
import styles from "./style.module.css";

type Props = {
  align: "left" | "right" | "center";
  pageSection?: boolean;
  reverseOnMobile?: boolean;
  mobileHorizontal?: boolean;
  verticalBottom?: boolean;
};

const Row: React.FC<Props> = ({
  children,
  align,
  pageSection,
  reverseOnMobile,
  mobileHorizontal,
  verticalBottom,
}) => {
  return (
    <div
      className={cx(styles.row, styles[align], {
        [styles.pageSection]: pageSection,
        [styles.reverseOnMobile]: reverseOnMobile,
        [styles.mobileHorizontal]: mobileHorizontal,
        [styles.verticalBottom]: verticalBottom,
      })}
    >
      {children}
    </div>
  );
};

export default Row;
